
import {Vue} from "vue-class-component";

export default class PlanoDados extends Vue{

  nome = '';
  retrievedObject: any;
  cadastroObj: any;

  card = {
    plano: '',
    nome: '',
    cpf: '',
    cardNumber: '',
    cvv: '',
    data: '',
    parcelas: ''
  }

  status = [{titulo: "Cursos Iniciados", value: 25, cursos: [{nome: 'Introduo ao Kotlin', valor: 0.2}, {nome: 'Python Indermediario', valor: 0.9}, {nome: 'C# + .NET', valor: 0.5}]},
    {titulo: "Cursos Concluidos", value: 32, cursos: [{nome: 'Java Iniciante', valor: 1}, {nome: 'Introduo a Logica de Programao', valor: 1}, {nome: 'C# basico', valor: 1}]}]

  dadosPlano: any;
  retrievedObjectPlano: any;

  cardsPlanos = [
    {icon: "school", nome_plano: "Plano Junior", desconto: "Desconto 10%", parcelas: "12X", preco: "R$85,99", condicao: " vista"},
    {icon: "lightbulb", nome_plano: "Plano Pleno", desconto: "Desconto 5%", parcelas: "12X", preco: "R$99,99", condicao: " vista"},
    {icon: "military_tech", nome_plano: "Plano Snior", desconto: "Desconto 3%", parcelas: "12X", preco: "R$199,99", condicao: " vista"}
  ]
  list = ["Acesso a todos os cursos da plataforma", "Desafios diarios", "Certificado", "Aulas de Ingls Gratuitas"]

  mounted() {
    if (localStorage.getItem('cadastro')) {
      this.retrievedObject = localStorage.getItem('cadastro');
      this.cadastroObj = JSON.parse(this.retrievedObject);

      this.nome = this.cadastroObj.name;

      if (localStorage.getItem('plano')) {
        this.retrievedObjectPlano = localStorage.getItem('plano');
        this.dadosPlano = JSON.parse(this.retrievedObjectPlano);
        this.card = this.dadosPlano
      }
    }
  }
}
