
import {Vue} from "vue-class-component";
import { Emit } from 'vue-property-decorator'
import axios from 'axios';

export default class ModalSingup extends Vue{

  cadastro = {
    nome: null,
    email: null,
    dataAniversario: null,
    senha: null,
    cpf: null,
    telefone: null,
  }

  checkbox = false;

  @Emit("close")
  close(){
    return false;
  }

  private axiosInstace = axios.create({
    baseURL: 'https://localhost:5011/api/v1/'
  })

    onSubmit(){
      if(!this.checkbox) {
        window.alert("Aceite os termos para se cadastrar !");
      }
      else {
        // window.localStorage.setItem ('cadastro', JSON.stringify (this.cadastro));
        // window.localStorage.setItem ('login', '1')
        // document.location.reload (true);
        return new Promise(
            () => {
              this.axiosInstace.post('/aluno', this.cadastro).then(
                  () => {
                    this.cadastro = {
                      nome: null,
                      email: null,
                      dataAniversario: null,
                      senha: null,
                      cpf: null,
                      telefone: null,
                    };
                  }
              )
            });
      }

  }

  onReset () {
    this.cadastro = {
      nome: null,
      email: null,
      dataAniversario: null,
      senha: null,
      cpf: null,
      telefone: null,
    }
  }
  mounted(){
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }
}
